import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import closeIcon from '../imgs/closeIcon.png';

const ContactPage = () => {
    const [showModal, setShowModal] = useState(false);
    const [formData, setFormData] = useState({
        title: '',
        message: '',
        contact: '',
        email: '',
        files: [],
    });
    const server_url = process.env.REACT_APP_EMAIL_SERVER_URL;
    const navigate = useNavigate();

    const toggleModal = () => {
        setShowModal(!showModal);
    };

    const handleChange = (e) => {
        const { name, value, files } = e.target;
        if (name === 'file') {
            setFormData((prev) => ({ ...prev, files: Array.from(files) }));
        } else {
            setFormData((prev) => ({ ...prev, [name]: value }));
        }
    };

    const handleSubmit = async (e) => {
        e.preventDefault();

        const formDataToSend = new FormData();

        //   
        formDataToSend.append('title', formData.title);
        formDataToSend.append('message', formData.message);
        formDataToSend.append('contact', formData.contact);
        formDataToSend.append('email', formData.email);

        //  
        if (formData.files.length > 0) {
            formData.files.forEach((file) => {
                formDataToSend.append('files', file);
            });
        }

        alert('  !');
        navigate("/");

        axios.post(`${server_url}/send-email`, formDataToSend, {
            headers: { 'Content-Type': 'multipart/form-data' }
        }).catch(() => {
            alert('    .');
        });
    };

    const handleCancel = () => {
        navigate("/");
    };

    useEffect(() => {
        if (showModal) {
            document.body.style.overflow = "hidden"; //    
        } else {
            document.body.style.overflow = "auto"; //     
        }

        return () => {
            document.body.style.overflow = "auto"; //     
        };
    }, [showModal]);

    return (
        <FormContainer onSubmit={handleSubmit}>
            <Title></Title>
            <Divider />
            <MiddleContainer>
                <Instructions>
                             . <br />
                      , ,   . <br />
                         
                </Instructions>
                <FormGroup>
                    <Label><span>Title</span></Label>
                    <Input type="text" name="title" value={formData.title} onChange={handleChange} required />
                </FormGroup>
                <FormGroup>
                    <Label><span>Description</span></Label>
                    <TextArea name="message" value={formData.message} onChange={handleChange} required />
                </FormGroup>
                <FormGroup>
                    <Label><span>Phone</span></Label>
                    <SmallInput type="tel" name="contact" value={formData.contact} placeholder=": 010-1234-5678" onChange={handleChange} required />
                </FormGroup>
                <FormGroup>
                    <Label><span>Email</span></Label>
                    <SmallInput type="email" name="email" value={formData.email} placeholder=": ex@naver.com" onChange={handleChange} required />
                </FormGroup>
                <FormGroup style={{ borderBottom: '1px solid #e9e9e9', paddingBottom: '1rem' }}>
                    <Label><span>File upload</span></Label>
                    <FileGroup>
                        <Input type="file" name="file" onChange={handleChange} style={{ border: 'none', width: '400px' }} multiple />
                        <span>   25MB , Excel, MS Word, HWP, PDF, PNG, GIF, JPG    .</span>
                    </FileGroup>
                </FormGroup>
                <Agreement>
                    <Checkbox type="checkbox" id="agreement" required />
                    <label htmlFor="agreement">
                            . <span style={{ color: 'red' }}>*</span>
                    </label>
                    <ViewDetails type="button" onClick={toggleModal}></ViewDetails>
                </Agreement>
                <ButtonGroup>
                    <SubmitButton></SubmitButton>
                    <CancelButton onClick={handleCancel}></CancelButton>
                </ButtonGroup>
            </MiddleContainer>
            {showModal && (
                <ModalOverlay onWheel={(e) => e.preventDefault()}>
                    <Modal onWheel={(e) => e.stopPropagation()}>
                        <ModalTopContainer>
                            <ModalHeader></ModalHeader>
                            <CloseButton onClick={toggleModal}><img src={closeIcon} /></CloseButton>
                        </ModalTopContainer>
                        <ModalContent>
                            <p>
                                ( '' )   30    ,
                                          
                                   .
                                <br /><br />
                                <span>1 ( )</span>
                                <br />
                                     .     
                                  ,        18 
                                       .
                                <br /><br />
                                1.    
                                 :          ,        .
                                <br />
                                      :            .
                                <br />
                                 : ,             .
                                <br /><br />
                                2.  
                                :          .
                                <br />
                                :  ,    ,     .
                                <br /><br />
                                3. / 
                                      (3)  ,           .
                                <br />
                                      . ,            .
                                <br /><br />
                            </p>
                        </ModalContent>
                    </Modal>
                </ModalOverlay>
            )}
        </FormContainer>
    );
};

const FormContainer = styled.form`
    padding: 40px;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: hidden;
`;

const Title = styled.h3`
    font-size: 26px;
    font-weight: 600;

    @media screen and (max-width: 1100px) {
      font-size: 23.5px;
    }

    @media screen and (max-width: 700px) {
      font-size: 21px;
    }
`;

const Divider = styled.div`
    width: 1000px;
    height: 1px;
    margin: 0 auto;
    background-color: #ddd;
    margin-top: 20px;
    margin-bottom: 60px;

    @media screen and (max-width: 1100px) {
      width: 700px;
    }

    @media screen and (max-width: 700px) {
      width: 90%;
    }
`;

const MiddleContainer = styled.div`
    width: 900px;

    @media screen and (max-width: 1100px) {
      width: 500px;
    }

    @media screen and (max-width: 700px) {
      width: 90%;
    }
`;

const Instructions = styled.p`
    font-size: 14px;
    color: #555;
    margin-bottom: 20px;
    text-align: left;
`;

const FormGroup = styled.div`
    width: 880px;
    margin-bottom: 20px;
    display: flex;
    flex-direction: row;
    border-top: 1px solid #e9e9e9;
    padding-top: 1rem;
    padding-left: 2 rem;

    @media screen and (max-width: 1100px) {
        width: 500px;
        flex-direction: column;
    }

    @media screen and (max-width: 700px) {
        width: 100%;
    }
`;

const Label = styled.label`
    width: 180px;
    display: block;
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 8px;
    color: #333;
    text-align: left;

    span {
        font-size: 12px;
        padding-left: 0.5rem;
        font-weight: 500;
    }
`;

const Input = styled.input`
    width: 700px;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
    font-size: 14px;

    @media screen and (max-width: 1100px) {
      width: 500px;
    }

    @media screen and (max-width: 700px) {
      width: 100%;
    }
`;

const SmallInput = styled.input`
    width: 400px;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
    font-size: 14px;

    @media screen and (max-width: 1100px) {
      width: 500px;
    }

    @media screen and (max-width: 700px) {
      width: 100%;
    }
`;

const TextArea = styled.textarea`
    width: 700px;
    height: 300px;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
    font-size: 14px;
    min-height: 100px;

    @media screen and (max-width: 1100px) {
      width: 500px;
      height: 300px;
    }

    @media screen and (max-width: 700px) {
      width: 100%;
    }
`;

const FileGroup = styled.div`
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    
    span {
        font-size: 13px;
        padding-left: 10px;
    }
`;

const Agreement = styled.div`
    display: flex;
    align-items: center;
    gap: 10px;

    label {
        font-size: 13px;
    }
`;

const Checkbox = styled.input`
    margin-right: 10px;
`;

const ViewDetails = styled.button`
    background: none;
    border: none;
    color: #808080;
    text-decoration: underline;
    cursor: pointer;

    @media screen and (max-width: 700px) {
      font-size: 13px;
    }
`;


const ButtonGroup = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 50px;
    padding-top: 2rem;

    @media screen and (max-width: 1100px) {
      flex-direction: column;
      gap: 20px;
    }
`;

const CancelButton = styled.button`
    width: 320px;
    padding: 10px 20px;
    background: #6a6a6a;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 18px;
    color: white;

    @media screen and (max-width: 1100px) {
      width: 320px;
    }

    @media screen and (max-width: 700px) {
      width: 250px;
    }
`;

const SubmitButton = styled.button`
    width: 320px;
    padding: 10px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 4px;
    font-size: 18px;
    cursor: pointer;

    &:hover {
        background-color: #0056b3;
    }

    @media screen and (max-width: 1100px) {
      width: 320px;
    }

    @media screen and (max-width: 700px) {
      width: 250px;
    }
`;

const ModalOverlay = styled.div`
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
`;

const Modal = styled.div`
    background: white;
    border-radius: 8px;
    padding: 20px;
    width: 500px;
    height: 700px;
    overflow-y: auto;

    @media screen and (max-width: 700px) {
        width: 400px;
        height: 600px;
    }
`;

const ModalTopContainer = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
`;

const CloseButton = styled.button`
    width: 20px;
    height: 20px;
    color: black;
    background: none;
    border: none;
    cursor: pointer;
    font-size: 30px;
    margin-top: -1.2rem;

    img {
        width: 20px;
        height: 20px;
    }
`;

const ModalHeader = styled.h2`
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 40px;
    text-align: left;
`;

const ModalContent = styled.div`
    font-size: 14px;
    line-height: 1.6;
    text-align: left;
    
    p {
        span {
            font-weight: bold;
        }
    }
`;

export default ContactPage;
